.matchList-container {
    margin: auto;
    width: 60%;
    padding: 30px;
    background: grey;
    box-shadow: 0 15px 30px -10px transparentize(#000, 0.9);
    
    outline: none;
    border-radius: 15px;

    .wrapper {
        
        padding: 5px 0;
        display: flex;
        justify-content: space-around;
       
        .overview, .game-results, .game-info {
            flex-direction: column;
            .summ-spells{
                display: flex;
            }
            .items-container {
                height:30%;
                background: honeydew;
            }
        }
    }
}